/* css for the header */
header {
  padding-top: 15px;
  text-align: center;
  font-size: 50px;
}

/* To take away the bullets in nav bar */
header nav ul {
  list-style-type: none;
}

header nav ul li {
  display: inline-block;
  margin-left: 15px;
}

nav {
  font-family: 'Poiret One', cursive;
  font-size: 15px;
  text-align: center;

}
