/* css for the body */
body {
  background-color: #0D1317;
  font-family: 'Poiret One', cursive;
  margin: 0;
  padding: 0;
}

.almond {
  color: #f2e2d2
}

/* This code is to show the mainSlide only */
.container {
  height: 100vh;
  background-color: #0D1317;
  box-sizing: border-box;
}

#aboutSlide {
  height: 100vh;
  width: 100vw;
  /*needed to add a 'vw' because we wanted the width, so that it will match the height 'vh' */
  /* position:absolute; /*needed to make the position absolute so that we could move it all the way to the left
    left:0; this means it HAS to start at the start of the left-side browser, starting at pixel zero */
  background-color: #f2e2d2;
  color: #0D1317;
  box-sizing: border-box;
  text-align: center;
  padding-top: 10px;
}

/*; <--do not put a semicolon at the end of the curly brace, will affect the code below it*/

#workSlide {
  height: 100vh;
  width: 100vw;
  /*needed to add a 'vw' because we wanted the width, so that it will match the height 'vh'*/
  /* position:absolute; /*needed to make the position absolute so that we could move it all the way to the left 
    left:0; /*this means it HAS to start at the start of the left-side browser, starting at pixel zero */
  background-color: #0D1317;
  color: #f2e2d2;
  box-sizing: border-box;
  text-align: center !important;
}

/* CSS for the contact page- page is only showing up at the very bottom. Need to look further into. */
#Contact-Me{
  height: 100vh;
  width: 100vw;
  background-color: #0D1317;
  color: #f2e2d2;
  box-sizing: border-box;
  text-align: center !important;
  /* list-style-type: none; */
  }

/* vertical 'AMY'-mainslide */
.verticalName {
  writing-mode: vertical-rl;
  text-orientation: upright;
  font-size: 100px;
  font-family: 'Poiret One', cursive;
  padding-top: 100px;
  padding-left: 300px;
  text-align: center;
}


#About-Me,
#Work {
  font-size: 30px;
  font-weight: bolder;
  margin-top: 3vh;
}

.card-text {
  font-size: 1.2rem !important;
  line-height: 2.5 !important;
  text-align: justify !important;
}

.card-title {
  text-align: justify !important;
}


